body {
  font-family: "Poppins", Arial, sans-serif;
  background: #fff;
}
/* CSS của phần Top Header -------------------------------------------------------------------------------------------------*/
.topbar {
  width: 99vw;
  background: #323232;
  color: #fff;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: Arial, sans-serif;
  font-size: 16px;
  position: relative;
  z-index: 100;
}

.topbar-left {
  display: flex;
  align-items: center;
  height: 100%;
  padding-left: 20px;
}

.colorlib-logo {
  font-weight: bold;
  letter-spacing: -1px;
  font-size: 20px;
  color: #fff;
}

.colorlib-dot {
  color: #80e36b;
  font-size: 22px;
}

.topbar-title {
  margin-left: 21px;
  color: #bdbdbd;
  font-size: 16px;
  letter-spacing: 1px;
  background-color: #222222;
  padding: 20px 35px 20px 20px;
  font-family: "Open Sans", sans-serif;
}

.topbar-right {
  display: flex;
  align-items: center;
  height: 100%;
  padding-right: 3px;
}

.topbar-right:hover {
  background-color: #333333;
}

.topbar-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 100%;
  font-size: 24px;
  color: #bdbdbd;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
}

/* Hover cho tất cả các icon */
.topbar-icon:hover {
  background-color: #222222;
  color: #ffffff;
}

/* Hover cho cart khi active */
.topbar-icon.cart.active:hover {
  background-color: #222222;
  color: #fff;
}

.topbar-icon.cart.active {
  width: 60px;
  font-size: 18px;
  background: #7ac64d;
  color: #fff;
  border-radius: 0;
}

.topbar-icon.cart.active:hover {
  color: #fff;
}

/* CSS của phần Header -------------------------------------------------------------------------------------------------*/
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 85.2px;
  background: transparent;
  position: relative;
  z-index: 2;
  background: linear-gradient(to right, #ebefff 50%, #fff 50%);
}

.logo {
  position: relative; /* Cho phép phần tử con dùng absolute dựa trên nó */
  display: flex;
  align-items: center;
  font-weight: 1000;
  font-size: 26px;
  margin-left: 12%;
}

.logo-circle {
  position: absolute;
  left: 0;
  z-index: 1;
  background: #3b5bfe;
  color: #fff;
  border-radius: 50%;
  width: 37px;
  height: 37px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 26px;
  margin-left: 15px;
  font-weight: 900;
  font-family: "PoppinsExtraBold", Arial, sans-serif;
}

.logo-text {
  position: relative;
  padding-left: 24px; /* chừa chỗ cho logo-circle */
  color: #22223b;
  z-index: 2;
  font-weight: 900;
  font-family: "PoppinsExtraBold", Arial, sans-serif;
}
.highlightR {
  color: white;
  font-weight: 900;
  font-family: "PoppinsExtraBold", Arial, sans-serif;
}

.header nav {
  display: flex;
  gap: 36px;
  margin-right: 15vw;
}

.header nav a {
  color: #22223b;
  text-decoration: none;
  font-size: 16px;
  transition: color 0.2s;
  font-family: "Poppins", Arial, sans-serif;
  padding: 0 0 2px;
  position: relative;
  display: inline-block;
  transition: color 0.3s ease;
  letter-spacing: 1px;
  font-weight: 400;
}
.header nav a::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) scaleX(0);
  transform-origin: center;
  width: 100%;
  height: 2px;
  background-color: #3b5bfe;
  transition: transform 0.3s ease;
}
.header nav a:hover::after {
  transform: translateX(-50%) scaleX(1);
}
.nav-link {
  display: block;
}

/* Phần giới thiệu nổi bật hero section---------------------------------------------------------------------------------*/
.hero {
  width: 1520.8px;
  min-height: 669px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin: auto;
  background: linear-gradient(to right, #ebefff 50%, #fff 50%);
}

.hero-content {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  text-align: center;
  position: relative;
  z-index: 1;
  margin-bottom: 140px;
}

.hero-subtitle {
  color: #3b5bfe;
  font-weight: 700;
  letter-spacing: 4px;
  font-size: 16px;
  margin-bottom: 18px;
  margin-top: 40px;
  font-family: "PoppinsCustom", sans-serif;
}

.hero-title {
  font-size: 60px;
  font-weight: 900;
  margin: 0 0 18px 0;
  color: #111;
  line-height: 1.1;
  font-family: "PoppinsExtraBold", Arial, sans-serif;
}

.hero-desc {
  font-size: 32px;
  font-weight: 700;
  color: #22223b;
  margin-bottom: 32px;
  font-family: "Poppins", Arial, sans-serif;
}

.hero-highlight {
  color: #3b5bfe;
  text-decoration: underline;
  font-weight: ̣900;
  font-size: 32px;
  font-family: "PoppinsExtraBold", Arial, sans-serif;
}

.hero-arrow-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
  position: relative;
  height: 70px;
}

.hero-arrow-wrapper::before {
  content: "";
  position: absolute;
  width: 68px;
  height: 68px;
  background: #ebefff;
  transform: rotate(45deg);
  z-index: 0;
}

.hero-arrow {
  position: relative;
  font-size: 30px;
  color: #3b5bfe;
  z-index: 1;
  animation: arrowDown 2.3s infinite;
  user-select: none;
  line-height: 1;
}

@keyframes arrowDown {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(12px);
  }
  100% {
    transform: translateY(0);
  }
} /*Hiệu ứng này em tham khảo ở https://tenten.vn/tin-tuc/transform-css-la-gi/*/

/* About Section */
.about {
  width: 1520.8px;
  background: #fff;
  padding: 0;
}

.about-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
}

.about-row {
  display: flex;
  align-items: stretch;
  background: #f8f9fa;
  box-shadow: 0 10px 40px rgba(60, 72, 88, 0.07);
