  font-size: 16px;
  margin-bottom: 40px;
  line-height: 1.8;
  font-family: "Regular400", Arial, sans-serif;
}

.about-info {
  list-style: none;
  padding: 0;
  margin: 0 0 30px 0;
}

.about-info li {
  margin-bottom: 12px;
  font-size: 16px;
  color: #999999;
  display: flex;
  line-height: 1.5;
