.about-info span {
  min-width: 130px;
  color: #111;
  margin-right: 10px;
  display: inline-block;
  font-family: "SemiBold600", Arial, sans-serif;
  line-height: 1.8;
}

.about-projects {
  font-size: 20px;
  margin-bottom: 32px;
  color: #222;
}

.about-number {
  color: #3e64ff;
  font-weight: 700;
  font-size: 22px;
  margin-right: 4px;
}

.about-btn {
  width: 120px;
  display: inline-block;
  background: #3e64ff;
  color: #fff;
  padding: 16.6px 15px 16.6px 15px;
  border-radius: 28px;
  font-weight: 100;
  font-size: 12px;
  text-decoration: none;
  letter-spacing: 2px;
  transition: background 0.2s;
  border: none;
  margin-top: 10px;
  box-shadow: 0 4px 18px rgba(78, 168, 222, 0.08);
  
}

.about-btn:hover {
  color: #22223b;
}

