/*Phần khoảng trắng*/
.resume-section {
  display: flex;
  width: 100%;
  padding: 70px 0 20px 0;
  background: #fff;
  font-family: "Poppins", Arial, sans-serif;
}

/* Menu trái */
.col-aside {
  width: 285px;
  min-width: 180px;
  margin-right: 50px;
  margin-left: 153px;
  padding-top: 12px;
}

.col-aside nav ul {
  list-style: none;
  padding: 20px 20px 0;
  margin: 0;
}

.col-aside nav ul li {
  margin-bottom: 24px;
}

.col-aside nav ul li:last-chcild {
  margin-bottom: 0;
}

.col-aside nav ul li a {
  color: #111;
  font-weight: bold;
  font-size: 16px;
  text-decoration: none;
  position: relative;
  padding-left: 32px;
  transition: color 0.2s;
  display: inline-block;
  letter-spacing: -0.5px;
}

.col-aside nav ul li.active a {
  color: #2563eb;
}

.col-aside nav ul li.active a::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 28px;
  height: 3px;
  background: #2563eb;
  border-radius: 2px;
}

.col-aside nav ul li a:hover {
  color: #2563eb;
}

/* Nội dung phải */
.col-center {
  flex: 1;
  min-width: 825px;
  max-height: 4285px;
}

.resume-title {
  color: #2563eb;
  font-size: 30px;
  font-weight: 800;
  margin-bottom: 25px;
}

.resume-class {
  display: flex;
  align-items: flex-start;
  margin-bottom: 40px;
  border-bottom: 1px solid #eee;
  padding-bottom: 40px;
}

.resume-class:last-child {
  border-bottom: none;
  padding-bottom: 0;
}

.icon {
  width: 50px;
  height: 50px;
  background: #2563eb;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 28px;
  flex-shrink: 0;
}

.icon span {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
}
.icon img {
  width: 28px;
  height: 28px;
  object-fit: contain;
}
.resume-time {
  color: #2563eb;
  font-weight: 700;
  font-size: 18px;
  margin-bottom: 4px;
}

.resume-position {
  font-size: 26px;
  font-weight: 700;
  color: #111;
  margin-bottom: 6px;
  line-height: 1.2;
}

.resume-place {
  font-size: 18px;
  font-weight: 700;
  color: #222;
  margin-bottom: 10px;
}

.resume-desc {
  color: #8b8b99;
  font-size: 16px;
  line-height: 1.2;
  width: 759px;
  height: 38.4px;
}
/*Phần page*/
.page1,
.page2,
.page3,
.page4 {
  margin-top: 40px;
  margin-bottom: 40px;
}
/*Phần đồ thị chart của page3*/
.resume-title {
  color: #3e64ff;
  font-size: 32px;
  font-weight: 700;
  margin-bottom: 30px;
  letter-spacing: 1px;
}

.skills-circular-row {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  margin-bottom: 40px;
  margin-right: 200px;
}

.skills-circular-card {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 6px 24px rgba(0, 0, 0, 0.07);
  padding: 32px 20px 24px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 0;
}

.skills-circular-title {
  font-size: 20px;
  font-weight: 700;
  color: #222;
  margin-bottom: 12px;
  letter-spacing: 1px;
}
.skills-circular-chart {
  position: relative;
  width: 170px;
  height: 170px;
  margin-bottom: 15px;
}

.skills-circular-chart svg {
  display: block;
  width: 170px;
  height: 170px;
  transform: rotate(-90deg);
}

.skills-circular-chart .bg {
  fill: none;
  stroke: #e9ecef;
  stroke-width: 10;
}

.skills-circular-chart .progress {
  fill: none;
  stroke: #3e64ff;
  stroke-width: 10;
  stroke-linecap: round;
  transition: stroke-dashoffset 1s cubic-bezier(0.77, 0, 0.18, 1);
}

.skills-circular-percent {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 36px;
  font-weight: 700;
  color: #222;
  line-height: 1;
}
.skills-circular-percent span {
  font-size: 16px;
  font-weight: 400;
  color: #888;
}

.skills-circular-meta {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-top: 10px;
  gap: 10px;
}

.skills-circular-meta-val {
  font-size: 24px;
  font-weight: 700;
  color: #222;
  text-align: center;
}

.skills-circular-meta-label {
  font-size: 12.8px;
  color: #888;
  text-align: center;
  margin-top: 2px;
}

/* Thanh kỹ năng ngang */
.skills-bar-row {
  display: flex;
  gap: 60px;
  justify-content: center;
  margin-top: 30px;
  flex-wrap: wrap;
  margin-right: 200px;
}

.skills-bar-col {
  flex: 1 1 300px;
  min-width: 280px;
  max-width: 420px;
}

.skills-bar-item {
  margin-bottom: 28px;
}

.skills-bar-label {
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 5px;
  color: #111;
}

.skills-bar-track {
  width: 400px;
  height: 10px;
  background: #eee;
  border-radius: 2px;
  overflow: hidden;
}

.skills-bar-progress {
  height: 100%;
  background: #3e64ff;
  border-radius: 2px;
  transition: width 0.8s cubic-bezier(0.77, 0, 0.18, 1);
}

/* Responsive */
@media (max-width: 900px) {
  .skills-circular-row {
    flex-direction: column;
    align-items: center;
    gap: 25px;
  }
  .skills-bar-row {
    flex-direction: column;
    gap: 0;
  }
  .skills-bar-col {
    max-width: 100%;
  }
}

/* Phần Research Responsive*/
@media (max-width: 768px) {
  .my-component {
    transform: scale(0.5);
    transform-origin: top left; /* giúp giữ vị trí ở góc */
  }
}
/*CSS của cái Service*/
.services-section {
  padding: 70px 0 60px 0;
  background: #fff;
  text-align: center;
}

.services-title {
  font-size: 48px;
  font-weight: 700;
  margin-bottom: 8px;
  letter-spacing: 1px;
}

.services-desc {
  color: #888;
  font-size: 18px;
  margin-bottom: 50px;
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  margin: 0 50px 0 50px;
}

.service-box {
  background: #fff;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
  padding: 45px 30px 35px 30px;
  text-align: center;
  transition: background 0.3s, box-shadow 0.3s;
  position: relative;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.service-box:hover {
  background: #f3f6fd;
  box-shadow: 0 12px 40px rgba(78, 115, 223, 0.13);
}

.service-icon {
  width: 60px;
  height: 60px;
  margin-bottom: 18px;
  object-fit: contain;
  filter: brightness(0) saturate(100%) invert(37%) sepia(77%) saturate(748%)
    hue-rotate(194deg) brightness(97%) contrast(95%);
}

.service-name {
  font-size: 16px;
  font-weight: 700;
  margin-bottom: 10px;
  color: #222;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.service-divider {
  width: 40px;
  height: 2px;
  background: #e9ecef;
  margin: 0 auto 18px auto;
}

.service-text {
  color: #666;
  font-size: 16px;
  line-height: 1.7;
  margin-bottom: 0;
  margin-top: 0;
}

/* Responsive: 2 cột khi <900px, 1 cột khi <600px */
@media (max-width: 1024px) {
  .service-box {
    width: 50%;
  }
}
@media (max-width: 700px) {
  .services-title {
    font-size: 36px;
  }
  .service-box {
    width: 100%;
    margin-bottom: 20px;
  }
}
