/*cái này giống phần Hero nhưng nó ở dưới*/
.freelance-section {
  position: relative;
  padding: 60px 0;
  text-align: center;
  margin: 60px 0 0 0;
  overflow: hidden;
  z-index: 0;
}

.freelance-section::before {
  content: "";
  position: absolute;
  inset: 0;
  background-image: url("../src//assets//image//bg_1.jpg");
  background-size: cover;
  background-position: center;
  opacity: 2;
  filter: blur(1px);
  z-index: 1;
}

.freelance-section::after {
  content: "";
  position: absolute;
  inset: 0;
  background: #3e64ff;
  opacity: 0.75;
  z-index: 2;
  height: 417.2px;
}

.freelance-content {
  position: relative;
  max-width: 700px;
  margin: 0 auto;
  color: #fff;
  z-index: 3;
}

.freelance-title {
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 18px;
  letter-spacing: 1px;
  color: #fff;
}

.freelance-desc {
  font-size: 18px;
  color: #e9ecef;
  margin-bottom: 30px;
  line-height: 1.7;
}

.freelance-btn {
  display: inline-block;
  padding: 13px 38px;
  font-size: 18px;
  font-weight: 600;
  color: #3e64ff;
  background: #fff;
  border-radius: 50px;
  text-decoration: none;
  box-shadow: 0 4px 16px rgba(62, 100, 255, 0.1);
  transition: background 0.2s, color 0.2s, box-shadow 0.2s;
  margin-top: 10px;
  border: none;
  cursor: pointer;
}

.freelance-btn:hover {
  background: #3e64ff;
  color: #fff;
  box-shadow: 0 8px 24px rgba(62, 100, 255, 0.18);
  border: 1px solid #fff;
}
